import { useRoutes } from 'react-router-dom'
import React from 'react'
import App from '@/views/App'
import About from '@/views/about'
import Mine from '@/views/mine'
export default function Index() {
  const route = useRoutes([
    {
      path: '/',
      element: <App />,
    },
    {
      path: '/about',
      element: <About />,
    },
    {
      path: '/mine',
      element: <Mine />,
    },
  ])

  return route
}
